
.hello{
  text-align:center;
  background:#2b332c;
  position:absolute;
  width:100%;
  height:100%;
}
.hello img{
  margin:20px auto 0 auto;
}
.vulenames{
  text-align:center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.vulenames p{
  font-size:30px;
  color:#fff;
  background: url(http://i.giphy.com/fsULJFFGv8X3G.gif);
  background-size: cover;
  background-position: center;
  -webkit-background-clip: text;
  color: transparent;
  font-weight: bold;
  margin:25px 0 25px 0;
}
.vulenames input{
  width:200px;
  height:26px;
  margin-top:5px;
  border-radius:2px;
  border:none;
  outline:none;
  padding-left:5px;
}
.vulenames button{
  background:red;
  width:100px;
  border:none;
  margin-left:1px;
  height:25px;
  margin-top:10px;
  border-radius:2px;
  color:#fff;
  outline:none;
}
.welcomes{
  color:#fff;
  font-size: 20px;
  line-height: 50px;
}




